<template>
    <div class="container flex-column">
        <div class="activity-page flex-column-grow" v-if="promotionList">
            <div class="mui-scroll">
                <ul>
                    <li @click="gotoLink(item.id)" v-for="(item) in promotionList" :key="item.id">
                        <h2>{{item.name}}</h2>
                        <p class="p" v-if="item.startTime && item.endTime">活动时间：{{item.startTime | timer1}} - {{item.endTime | timer1}}</p>
                        <p class="p" v-else>活动时间：永久</p>
                        <div class="pict">
                            <span>{{item.smallImageId}}</span>
                            <image-load :url="item.url"></image-load>
                        </div>
                        <div class="look"><b>查看详情</b></div>
                    </li>
                </ul>
            </div>
        </div>
        <loadding v-else></loadding>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    import { ACTIVITY_GET_PROMO_TION } from "@/tool/common/constants.js";

    export default {
        data(){
            return {
                user: {
                    uid:'',
                    token:''
                }
            }
        },
        created(){
            const {uid, token} = this.$route.query
            this.user.uid  = uid
            this.user.token  = token

            if(this.promotionList){
                console.log('已缓存');
            }else {
                this.loadData();
            }
        },
        mounted(){
           
        },
        computed: {
            ...mapState('Activity', {
                promotionList: state => state[ACTIVITY_GET_PROMO_TION].promotionList
            })
        },
        methods: {
            gotoLink(id){
                this.$router.push('/detail?id=' + id);
            },
            getImage(id, index){
                this.$api.upload.getImageData({id})
                .then(res=>{
                    this.$store.commit('Activity/SET_INDEX_URL',{index, url:'data:image/jpeg;base64,' + res.imageData})
                })
            },
            loadData(){
                this.$store.dispatch(`Activity/${ACTIVITY_GET_PROMO_TION}`, this.user)
                .then(res=>{
                    res.promotionList.forEach((item, index)=>{
                        this.getImage(item.smallImageId, index);
                        this.mui_scroll('.activity-page');
                    })
                })
            }
        }
    }
</script>

